.footer {
    width: 100%;
    padding: 4rem 0;
    background-color: rgba(19, 19, 19, 0.8);
    color: #fff;
  }
  
  .footer-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
  }
  
  .footer-section {
    padding: 0 1rem;
  }
  
  h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .contact-info,
  .social-links {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .contact-item,
  .social-links a {
    display: flex;
    align-items: center;
    color: #fff;
  }
  
  .contact-item svg,
  .social-links a svg {
    margin-right: 1rem;
  }

  .find-out-more {
    list-style: none;
    padding: 0;
  }
  
  .find-out-more li {
    margin-bottom: 0.5rem;
  }
  
  .find-out-more a {
    color: #fff;
    text-decoration: none;
  }
  
  .find-out-more a:hover {
    text-decoration: underline;
    color: gray;
  }

  .footer a {
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease; /* Adding transition effect for color change */
  }

  .hover-instagram:hover {
    color: #e84393;
  }

  .hover-tiktok:hover {
    color: #81ecec;
  }

  .hover-facebook:hover {
    color: #0984e3;
  }

  .hover-youtube:hover {
    color: #d63031;
  }
  
  @media screen and (max-width: 768px) {
    .footer-container {
      grid-template-columns: 1fr;
    }
  }
  