/* Create a flex container for the row */
.row {
  display: flex;
  flex-wrap: wrap; /* Allow items to wrap to the next line on smaller screens */
  padding: 15px;
}

.column {
  flex: 1;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-height: 600px; /* Adjust the height to your desired value */
  overflow: hidden; /* Prevent content from overflowing */
  text-align: center;
}

.about-heading {
  padding: 10px;
}

.about-me-image {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  display: block;
  margin: auto;
  transform: scale(0.8);
}

/* Media query for screens smaller than 768px (typical mobile devices) */
@media screen and (max-width: 768px) {
  .column {
    flex: 1 100%; /* Take up full width */
    max-height: none; /* Allow content to expand vertically */
  }

  .about-me-image {
    display: none; /* Hide the image on smaller screens */
  }
}
