.mask {
  width: 100;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.intro-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.6;
}

.mask::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: black;
  opacity: 0.6;
}

.primary-banner {
  height: 100%;
  width: 100%;
}

.primary-banner .content {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
}

.primary-banner .content h1 {
  font-size: 3rem;
  padding: 0.6rem 0 1.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.primary-banner .content p {
  font-size: 1.4rem;
  font-weight: 200;
  text-transform: uppercase;
}

.primary-banner .content h1 > span {
  font-weight: 1;
}

.btn-container {
  display: flex;
  justify-content: center;
}

.content .btn {
  margin: 1rem 0.2rem;
}

@media screen and (max-width: 640px) {
  .primary-banner .content h1 {
    font-size: 2rem;
  }

  .hero .content p {
    font-size: 1rem;
  }
}

@media screen and (max-width: 768px) {
  .btn-container {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 768px) {
  .primary-banner .content h1 {
    font-size: 4vw; /* Adjust font size for smaller screens */
    padding: 0.6rem 0 1rem; /* Adjust padding for smaller screens */
  }
}

@media screen and (min-width: 768px) and (max-width: 820px) {
  .primary-banner .content h1 {
    font-size: 3.5vw; /* Adjust font size for iPad Air screen size */
    padding: 0.6rem 0 1.2rem; /* Adjust padding for iPad Air screen size */
  }
}
