section {
  text-align: center;
  padding: 1.5em 1em;
}

.row {
  max-width: 90%;
  width: 100%;
  margin: 0 auto;
}

.services-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 0 auto;
  padding: 0 20px;
}

.heading {
    margin-bottom: 1em;
}

.figure {
  display: inline-block;
  margin: 10px;
  max-width: 280px;
  min-width: 230px;
  overflow: hidden;
  position: relative;
  text-align: center;
  width: 100%;
}

.figure * {
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}


.card-section {
  background: black;
  color: #efefef;
}

.card-section .figure {
  text-align: left;
  background: #1d1d1d;
  font-size: 16px;
}

.card-section .figure img {
  position: relative;
  max-width: 100%;
  vertical-align: top;
}

.card-section figcaption {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  padding: 20px 30px;
}

.card-section .figure figcaption:before,
.card-section .figure figcaption:after {
  width: 1px;
  height: 0;
}

.card-section .figure figcaption:before {
  right: 0;
  top: 0;
}

.card-section .figure figcaption:after {
  left: 0;
  bottom: 0;
}

.card-section .figure h3,
.card-section .figure p {
  line-height: 1.5em;
}

.card-section .figure h3 {
  margin: 0 0 5px;
  font-weight: 700;
  text-transform: uppercase;
}

.card-section .figure p {
  font-size: 0.8em;
  font-weight: 400;
  margin: 0 0 15px;
}

.card-section .figure a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.card-section .figure:before,
.card-section .figure:after,
.card-section .figure figcaption:before,
.card-section .figure figcaption:after {
  position: absolute;
  content: '';
  background-color: #efefef;
  z-index: 1;
  -webkit-transition: all 0.35s ease-in;
  transition: all 0.35s ease-in;
  opacity: 0.8;
}

.card-section .figure:before,
.card-section .figure:after {
  height: 1px;
  width: 0%;
}

.card-section .figure:before {
  top: 0;
  left: 0;
}

.card-section .figure:after {
  bottom: 0;
  right: 0;
}

.card-section .figure:hover img,
.card-section .figure.hover img {
  opacity: 0.4;
}

.card-section .figure:hover figcaption:before,
.card-section .figure.hover figcaption:before,
.card-section .figure:hover figcaption:after,
.card-section .figure.hover figcaption:after {
  height: 100%;
}

.card-section .figure:hover figcaption {
  opacity: 1;
}

.card-section .figure:hover:before,
.card-section .figure.hover:before,
.card-section .figure:hover:after,
.card-section .figure.hover:after {
  width: 100%;
}

.card-section .figure:hover:before,
.card-section .figure.hover:before,
.card-section .figure:hover:after,
.card-section .figure.hover:after,
.card-section .figure:hover figcaption:before,
.card-section .figure.hover figcaption:before,
.card-section .figure:hover figcaption:after,
.card-section .figure.hover figcaption:after {
  opacity: 0.1;
}

/* Footer */

footer.footer {
  text-align: center;
  color: #efefef;
  background: #1d1d1d;
  font-size: 0.8em;
  padding-bottom: 1.5em;
  font-style: italic;
}